import React from "react";
import PropTypes from "prop-types";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import Divider from "material-ui/Divider";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { api } from "../config/config";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";

function AddPokemonDialog(props) {
  const dispatch = useDispatch();
  const { trainer, onClose, selectedValue, open } = props;
  const pokemons = useSelector((state) => state.pokemon.pokemons);

  const handleClose = () => {
    onClose(selectedValue);
  };

  const handleListItemClick = (pokemon) => {
    onClose(pokemon);
    dispatch({
      type: "UPDATE_POKEMON",
      payload: axios.put(`${api}/trainer/${trainer._id}`, {
        method: "add",
        pokemon: pokemon,
      }),
    });
  };

  const listGenerator = () => {
    if (pokemons.map) {
      return pokemons.map((pokemon) => (
        <ListItem
          button
          onClick={() => handleListItemClick(pokemon)}
          key={pokemon.id}
        >
          <ListItemText
            primary={`Name: ${pokemon.name}, Move: ${pokemon.move}, Type: ${pokemon.type}`}
          />
        </ListItem>
      ));
    }
  };

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby="simple-dialog-title"
      open={open}
    >
      <DialogTitle id="simple-dialog-title">Choose a New Pokemon</DialogTitle>
      <MuiThemeProvider>
        <Divider variant="middle" />
      </MuiThemeProvider>
      <List>{listGenerator()}</List>
    </Dialog>
  );
}

AddPokemonDialog.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

export default AddPokemonDialog;
